<template>
  <div class="fileDeatil_answer_box">
    <div class="fileDeatil_form_box">
      <div class="fileDeatil_form_item">
        <MyForm
          ref="healthfileForm"
          :form="fileDeatilFormAttr"
          :formItem="formItem"
          :formData="formData"
          @submit="getsubmit"
        >
          <fksssForm
            slot="fksss"
            ref="fksssForm"
            :fksssData="formData.fuke_showshu_historys"
            @removeFksss="removeFksssFun"
            @addFksss="addFksssData"
          >
          </fksssForm>
          <sxsForm
            slot="sxs"
            ref="sxsForm"
            :sxsData="formData.shuxue_historys"
            @addSxs="addSxsData"
            @removeSxs="removeSxsFun"
          >
          </sxsForm>
          <ycsForm
            slot="ycs"
            ref="ycsForm"
            :ycsData="formData.yc_historys"
            @addYcs="addYcsData"
            @removeYcs="removeYcsFun"
          >
          </ycsForm>
          <div slot="updateInfo" class="align-left fontsize-12 color-orange">
            最近保存时间：{{ formatTime(formData.update_at) }}<br />
            修改人：{{ formData.update_user }}
          </div>
        </MyForm>
      </div>
    </div>
  </div>
</template>
<script>
import MyForm from '@/components/MyForm'
import fileDeatil from './basicInfo.js'
import fksssForm from './components/fksssForm'
import sxsForm from './components/sxsForm'
import ycsForm from './components/ycsForm'
import { updateOHealthFile } from '@/api/healthManagement'
import _ from 'lodash'

export default {
  props: {
    memberId: {
      type: String,
      default: ''
    },
    basicInfo: {
      type: Object,
      default: () => {}
    },
    dataChange: {
      type: Boolean,
      default: false
    },
    pageType: {
      type: String,
      default: ''
    },
    btnDisabled: {
      type: Boolean,
      default: false
    }
  },
  data() {
    // 表单配置
    var fileDeatilFormAttr = {
      showFormBorder: false,
      showTitle: false,
      ref: 'healthfileForm',
      labelWidth: '120px',
      labelPositon: 'right',
      style: 'notprepend',
      column: 1,
      hasSubmit: true,
      userId: '',
      relativeId: '',
      cancleShow: false,
      submitText: '修改并保存',
      cancleText: ''
    }
    return {
      fileDeatilFormAttr: fileDeatilFormAttr,
      fksssFormValid: false,
      ycsFormValid: false,
      sxsFormValid: false,
      formItem: [],
      formData: {}
    }
  },
  components: {
    MyForm,
    fksssForm,
    ycsForm,
    sxsForm
  },
  watch: {
    basicInfo: {
      handler: function(val) {
        if (this.dataChange) {
          this.formInit()
          this.getData()
        }
      },
      deep: true
    },
    btnDisabled(val) {
      this.fileDeatilFormAttr.submitDisabled = val
    }
  },
  created() {
    this.formInit()
    this.fileDeatilFormAttr.submitDisabled = this.btnDisabled
  },
  methods: {
    addSxsData() {
      const initData = {
        desc: '',
        at: ''
      }
      this.formData.shuxue_historys.push(initData)
    },
    addFksssData() {
      const initData = {
        name: '',
        at: ''
      }
      this.formData.fuke_showshu_historys.push(initData)
    },
    addYcsData() {
      const initData = {
        hy_at: '',
        is_gwy: '',
        fenmian_status: '',
        fenmian_at: '',
        fenmian_mode: '',
        is_ceqie: ''
      }
      this.formData.yc_historys.push(initData)
    },
    getData() {
      let data = Object.assign(
        {},
        this.formData,
        this.basicInfo.yuejing,
        this.basicInfo.chanke_history,
        this.basicInfo.baby_info
      )
      if (
        !this.basicInfo.chanke_history ||
        !this.basicInfo.chanke_history.yc_historys
      ) {
        data.yc_historys = [
          {
            hy_at: '',
            is_gwy: '',
            fenmian_status: '',
            fenmian_at: '',
            fenmian_mode: '',
            is_ceqie: ''
          }
        ]
      }
      if (
        !this.basicInfo.chanke_history ||
        !this.basicInfo.chanke_history.shuxue_historys
      ) {
        data.shuxue_historys = [
          {
            desc: '',
            at: ''
          }
        ]
      }
      if (
        !this.basicInfo.chanke_history ||
        !this.basicInfo.chanke_history.fuke_showshu_historys
      ) {
        data.fuke_showshu_historys = [
          {
            name: '',
            at: ''
          }
        ]
      }
      data.update_user = this.basicInfo.update_user
      data.update_at = this.basicInfo.update_at
      this.formData = data
      this.$emit('dataChange', false)
    },
    removeFksssFun(parentInd) {
      this.formData.fuke_showshu_historys = _.remove(
        this.formData.fuke_showshu_historys,
        function(n, index) {
          return !(index === parentInd)
        }
      )
    },
    removeSxsFun(parentInd) {
      this.formData.shuxue_historys = _.remove(
        this.formData.shuxue_historys,
        function(n, index) {
          return !(index === parentInd)
        }
      )
    },
    removeYcsFun(parentInd) {
      this.formData.yc_historys = _.remove(this.formData.yc_historys, function(
        n,
        index
      ) {
        return !(index === parentInd)
      })
    },
    formInit(row, index) {
      this.formItem = fileDeatil.form.item
      if (this.pageType === 'chat') {
        this.formItem[1].colSpan = 18
        this.formItem[2].colSpan = 18
        this.formItem[3].colSpan = 18
        this.formItem[4].colSpan = 18
      }
      this.formData = fileDeatil.form.data
    },
    ycsValid() {
      const ycsFormRef = this.$refs.ycsForm.$children
      const that = this
      if (ycsFormRef.length) {
        _(ycsFormRef).forEach(function(item, value) {
          const obj =
            item.$children && item.$children.length
              ? item.$children[0].$refs
              : ''
          if (obj && obj.ycsForm) {
            obj.ycsForm.validate(childValid => {
              if (childValid) {
                that.ycsFormValid = true
              } else {
                that.ycsFormValid = false
                return
              }
            })
          } else {
            // that.ycsFormValid = true
          }
        })
      } else {
        that.ycsFormValid = true
      }
    },
    fksssValid() {
      const fksssFormRef = this.$refs.fksssForm.$children
      const that = this
      if (fksssFormRef.length) {
        _(fksssFormRef).forEach(function(item, value) {
          const obj =
            item.$children && item.$children.length
              ? item.$children[0].$refs
              : ''
          if (obj && obj.fksssForm) {
            obj.fksssForm.validate(childValid => {
              if (childValid) {
                that.fksssFormValid = true
              } else {
                that.fksssFormValid = false
                return
              }
            })
          } else {
            // that.fksssFormValid = true
          }
        })
      } else {
        that.fksssFormValid = true
      }
    },
    sxsValid() {
      const sxsFormRef = this.$refs.sxsForm.$children
      const that = this
      if (sxsFormRef.length) {
        _(sxsFormRef).forEach(function(item, value) {
          const obj =
            item.$children && item.$children.length
              ? item.$children[0].$refs
              : ''
          if (obj && obj.sxsForm) {
            obj.sxsForm.validate(childValid => {
              if (childValid) {
                that.sxsFormValid = true
              } else {
                that.sxsFormValid = false
                return
              }
            })
          } else {
            // that.sxsFormValid = true
          }
        })
      } else {
        that.sxsFormValid = true
      }
    },
    getsubmit(val) {
      if (this.formData.has_fuke_shoushu_history) {
        this.fksssValid()
      } else {
        this.fksssFormValid = true
      }

      if (!this.fksssFormValid) {
        this.$message.error('请填写妇科手术史')
        return
      }
      if (this.formData.has_shuxue_history) {
        this.sxsValid()
      } else {
        this.sxsFormValid = true
      }
      if (!this.sxsFormValid) {
        this.$message.error('请填写输血史')
        return
      }
      if (this.formData.has_yc_history) {
        this.ycsValid()
      } else {
        this.ycsFormValid = true
      }
      if (!this.ycsFormValid) {
        this.$message.error('请填写孕产史')
        return
      }
      const yuejing = this.formData
      const chanke_history = this.formData
      const baby_info = this.formData
      // 修改并保存基本信息api
      const parmas = {
        yuejing: yuejing,
        baby_info: baby_info,
        chanke_history: chanke_history,
        update_key: 'chanke_basic_info'
      }
      const memberId =
        this.$route.params.id || this.memberId || this.basicInfo.member.id
      updateOHealthFile(memberId, parmas).then(res => {
        this.$message({
          type: 'success',
          message: '修改并保存成功'
        })
        if (!this.$route.params.id) {
          this.$store.dispatch('chat/getActivedUserInfo', {
            babyId: this.$store.state.chat.activedBabyId
          })
        }
        this.$emit('submitSuccess')
      })
    }
  }
}
</script>
<style lang="scss">
.fileDeatil_answer_box {
  width: 90%;
  .fileDeatil_form_item {
    position: relative;
    border-radius: 4px;
    margin-bottom: 20px;
  }
  .close_fileDeatil_icon {
    position: absolute;
    top: -10px;
    right: -10px;
    padding: 2px;
    color: #fff;
    background: #f8747f;
    border-radius: 50%;
    cursor: pointer;
  }
  p {
    text-align: center;
    margin-bottom: 20px;
  }
  .el-form--label-top .el-form-item__label {
    width: 100%;
    text-align: center;
  }
  .width80 {
    width: 85%;
  }
  .answer_box {
    .el-form-item {
      margin-bottom: 15px;
    }
  }
}
</style>
