<template>
  <div class="crq_form">
    <MyForm
      :form="form"
      ref="crqForm"
      :formData="formData"
      :formItem="formItem"
      @submit="submit"
      @resetForm="reset"
    >
      <div slot="updateInfo" class="align-left fontsize-12 color-orange">
        最近保存时间：{{ formatTime(formData.update_at) }}<br />
        修改人：{{ formData.update_user }}
      </div>
    </MyForm>
  </div>
</template>
<script>
import { getFormField } from '../../getField'
import MyForm from '@/components/MyForm'

export default {
  data() {
    // 表单配置
    var crqForm = {
      showFormBorder: false,
      showTitle: false,
      ref: 'crqForm',
      labelWidth: '150px',
      labelPositon: 'right',
      style: 'notprepend',
      column: 1,
      hasSubmit: true,
      btnRight: false,
      cancleShow: false,
      submitText: '修改并保存',
      cancleText: ''
    }
    return {
      searchParams: {},
      formItem: [],
      formData: {},
      form: crqForm
    }
  },
  props: {
    crqInfo: {
      type: Object,
      default: () => {}
    },
    btnDisabled: {
      type: Boolean,
      default: false
    }
  },
  components: {
    MyForm
  },
  created() {
    this.formInit()
    this.form.submitDisabled = this.btnDisabled
  },
  watch: {
    crqInfo: {
      handler: function(val) {
        this.formInit()
        this.getData()
      },
      deep: true
    },
    btnDisabled(val) {
      this.form.submitDisabled = val
    }
  },
  methods: {
    getData() {
      let data = Object.assign({}, this.crqInfo)
      this.formData = data
    },
    submit(val) {
      this.$emit('crqSubmit', val)
    },
    formInit(row) {
      this.formItem = getFormField('crqForm', 'item')
      this.formData = row ? row : getFormField('crqForm', 'data')
    },
    reset() {
      this.$emit('searchFun')
    }
  }
}
</script>
<style lang="scss" scoped>
.crq_form {
  width: 50%;
}
</style>
