<template>
  <div class="chanke_health_file_detail">
    <div>
      <span style="vertical-align: middle;display: inline-block;">
        <el-avatar size="medium" :src="userInfo.avatar"></el-avatar>
      </span>
      <span class="margin-right-30"> 用户：{{ userInfo.nickname }} </span>
      <div :class="memberId ? 'wrap' : 'no_wrap'">
        <span class="margin-right-30"> 患者姓名：{{ member.name }} </span>
        <span> 当前阶段：{{ curStageTxt(cur_stage) }} </span>
      </div>
    </div>
    <el-tabs v-model="activeName" style="margin-top:20px;">
      <el-tab-pane name="basic_info">
        <span slot="label">基本信息</span>
        <div class="patient_info">
          <div>
            <span class="span-key">患者姓名：</span>
            <span class="span-val">
              {{ member.name }}
            </span>
          </div>
          <div>
            <span class="span-key">出生日期：</span>
            <span class="span-val">
              {{ new Date(member.birthday) | formatTime('{y}-{m}-{d}') }}
            </span>
          </div>
          <div>
            <span class="span-key">年龄： </span>
            <span class="span-val">
              {{ member.age_of_month_value.year }}岁
            </span>
          </div>
          <div>
            <span class="span-key">职业： </span>
            <span class="span-val">
              {{ member.chanke.profession || '--' }}
            </span>
          </div>
        </div>
        <basicInfoForm
          :basicInfo="chankeBasicInfo"
          :dataChange="dataChange"
          @dataChange="setDataChange"
          :memberId="memberId"
          :pageType="pageType"
          :btnDisabled="!(role === 'hm')"
          @submitSuccess="getDetail"
        ></basicInfoForm>
      </el-tab-pane>
      <el-tab-pane name="byz">
        <span slot="label">
          <svg-icon v-show="cur_stage === 'byz'" icon-class="select"></svg-icon>
          备孕中
        </span>
        <byzForm
          :byzInfo="byzInfo"
          :btnDisabled="!(cur_stage === 'byz' && role === 'hm')"
          @byzSubmit="formSubmit"
        ></byzForm>
      </el-tab-pane>
      <el-tab-pane name="hyz">
        <span slot="label"
          ><svg-icon
            v-show="cur_stage === 'hyz'"
            icon-class="select"
          ></svg-icon>
          怀孕中</span
        >
        <hyzForm
          :hyzInfo="hyzInfo"
          @hyzSubmit="formSubmit"
          :btnDisabled="!(cur_stage === 'hyz' && role === 'hm')"
        ></hyzForm>
      </el-tab-pane>
      <el-tab-pane name="crq">
        <span slot="label"
          ><svg-icon
            v-show="cur_stage === 'crq'"
            icon-class="select"
          ></svg-icon>
          产褥期</span
        >
        <crqForm
          :crqInfo="crqInfo"
          :btnDisabled="!(cur_stage === 'crq' && role === 'hm')"
          @crqSubmit="formSubmit"
        ></crqForm>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
import { getOHealthFileDetail, updateOHealthFile } from '@/api/healthManagement'
import basicInfoForm from './basicInfoForm'
import byzForm from './byzForm'
import hyzForm from './hyzForm'
import crqForm from './crqForm'
import { curStageTxt } from '@/filters'
import { mapGetters } from 'vuex'
export default {
  props: {
    memberId: {
      type: String,
      default: ''
    },
    department: {
      type: String,
      default: ''
    },
    pageType: {
      type: String,
      default: ''
    }
  },
  computed: {
    ...mapGetters(['role'])
  },
  data() {
    return {
      activeName: 'basic_info',
      cur_stage: '',
      userInfo: '',
      chankeBasicInfo: {},
      member: {
        remark: '',
        name: '',
        age: '',
        avatar: '',
        age_of_month_value: {
          year: ''
        },
        chanke: {
          profession: ''
        }
      },
      byzInfo: {},
      hyzInfo: {},
      crqInfo: {},
      dataChange: false
    }
  },
  components: {
    basicInfoForm,
    byzForm,
    hyzForm,
    crqForm
  },
  created() {
    this.getDetail()
  },
  watch: {
    activeName() {
      this.getDetail()
    },
    memberId(val, oldVal) {
      if (val !== oldVal) {
        this.getDetail()
      }
    }
  },
  methods: {
    setDataChange(val) {
      this.dataChange = val
    },
    getDetail() {
      if (!this.$route.params.id && !this.memberId) {
        return
      }
      if (this.memberId && this.department !== 'chanke') {
        return
      }
      getOHealthFileDetail(
        this.$route.params.id || this.memberId,
        'chanke_' + this.activeName
      ).then(res => {
        if (res) {
          this.member = res.data.member
          this.userInfo = res.data.user_info
          this.cur_stage = res.data.cur_stage
          if (this.activeName === 'basic_info') {
            this.chankeBasicInfo = res.data.chanke_basic_info
            this.dataChange = true
          } else if (this.activeName === 'byz') {
            this.byzInfo = res.data.chanke_byz
          } else if (this.activeName === 'hyz') {
            this.hyzInfo = res.data.chanke_hyz
          } else {
            this.crqInfo = res.data.chanke_crq
          }
        }
      })
    },
    curStageTxt,
    formSubmit(val) {
      let params = {
        update_key: 'chanke_' + this.activeName
      }
      params = Object.assign({}, params, val)
      const id = this.memberId || this.$route.params.id
      updateOHealthFile(id, params).then(res => {
        this.$message({
          type: 'success',
          message: '修改并保存成功'
        })
        if (!this.$route.params.id) {
          this.$store.dispatch('chat/getActivedUserInfo', {
            babyId: this.$store.state.chat.activedBabyId
          })
        }
        this.getDetail()
      })
    }
  }
}
</script>
<style lang="scss" scoped>
.chanke_health_file_detail {
  overflow-x: auto;
  margin-left: 10px;
  padding-top: 10px;
  min-width: 650px;
}
.no_wrap {
  display: inline-block;
}
.wrap {
  margin-top: 5px;
}
.patient_info {
  .span-key {
    display: inline-block;
    min-width: 108px;
    line-height: 26px;
    font-size: 14px;
    font-family: PingFangSC-Regular;
    text-align: right;
    // color: #586378;
    color: #17233d;
    letter-spacing: 0;
  }
  .span-val {
    line-height: 26px;
    font-size: 14px;
    font-family: PingFangSC-Regular;
    // color: #747d8f;
    color: #17233d;
    letter-spacing: 0;
  }

  margin-bottom: 10px;
}
</style>
