<template>
  <div class="sxs_box">
    <div v-if="sxsData.length">
      <el-row v-for="(item, index) in sxsData" :key="index + 'sxs'">
        <div class="sxs_form_box">
          <div class="form_item">
            <i
              v-if="index > 0"
              class="el-icon-close close_baby_icon"
              @click="removesxs(index)"
            />
            <MyForm
              :myRef="'sxsForm'"
              :form="sxsFormAttr"
              :formItem="formItem"
              :formData="item"
            ></MyForm>
          </div>
        </div>
      </el-row>
      <el-row>
        <p class="add_answer_btn" @click="addsxsFun()">
          <i class="el-icon-plus" />添加
        </p>
      </el-row>
    </div>
  </div>
</template>
<script>
import MyForm from '@/components/MyForm'
import sxsJson from './sxsJson'

export default {
  props: {
    sxsData: {
      type: Array,
      default: () => []
    }
  },
  data() {
    // 表单配置
    var sxsFormAttr = {
      showFormBorder: false,
      showTitle: false,
      labelWidth: '90px',
      labelPositon: 'right',
      style: 'notprepend',
      column: 1,
      hasSubmit: false,
      btnRight: true,
      userId: '',
      relativeId: '',
      cancleShow: false,
      submitText: '确认提交',
      cancleText: ''
    }
    return {
      sxsFormAttr: sxsFormAttr,
      formItem: []
    }
  },
  components: {
    MyForm
  },
  created() {
    this.formInit()
  },
  methods: {
    formInit(row, index) {
      this.formItem = sxsJson.form.item
    },
    addsxsFun() {
      this.$emit('addSxs')
    },
    removesxs(parentInd) {
      this.$emit('removeSxs', parentInd)
    }
  }
}
</script>
<style lang="scss">
.sxs_box {
  width: 80%;
  min-width: 540px;
  .sxs_form_box {
    margin: 0 auto;
  }
  .form_item {
    position: relative;
    padding: 15px 0 0;
    background: #f5f7fa;
    border: 1px solid #e8eaec;
    border-radius: 4px;
    margin-bottom: 20px;
  }
  .close_baby_icon {
    position: absolute;
    top: -10px;
    right: -10px;
    padding: 2px;
    color: #fff;
    background: #f8747f;
    border-radius: 50%;
    cursor: pointer;
  }
  p {
    text-align: center;
    margin-bottom: 20px;
  }
  .el-form--label-top .el-form-item__label {
    width: 100%;
    text-align: center;
  }
  .width80 {
    width: 85%;
  }
  .del_btn {
    margin-left: 15px;
    font-size: 16px;
    color: #576277;
    border-radius: 4px;
    cursor: pointer;
  }
  .answer_box {
    .el-form-item {
      margin-bottom: 15px;
    }
  }
  .add_answer_btn {
    padding: 1px 0;
    width: 100px;
    text-align: center;
    color: #0085ff;
    background: #fff;
    border: 1px dotted #0085ff;
    border-radius: 4px;
    cursor: pointer;
  }
}
</style>
