<template>
  <div class="byz_form">
    <MyForm
      :form="form"
      ref="byzForm"
      :formData="formData"
      :formItem="formItem"
      @submit="byzSubmit"
      @resetForm="reset"
    >
      <div slot="updateInfo" class="align-left fontsize-12 color-orange">
        最近保存时间：{{ formatTime(formData.update_at) }}<br />
        修改人：{{ formData.update_user }}
      </div>
    </MyForm>
  </div>
</template>
<script>
import { getFormField } from '../../getField'

import MyForm from '@/components/MyForm'
export default {
  data() {
    // 表单配置
    var byzForm = {
      showFormBorder: false,
      showTitle: false,
      ref: 'byzForm',
      labelWidth: '150px',
      labelPositon: 'right',
      style: 'notprepend',
      column: 1,
      hasSubmit: true,
      btnRight: false,
      cancleShow: false,
      submitText: '修改并保存',
      cancleText: ''
    }
    return {
      searchParams: {},
      formItem: [],
      formData: {},
      form: byzForm
    }
  },
  props: {
    byzInfo: {
      type: Object,
      default: () => {}
    },
    btnDisabled: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    byzInfo: {
      handler: function(val) {
        this.formInit()
        this.getData()
      },
      deep: true
    },
    btnDisabled(val) {
      this.form.submitDisabled = val
    },
    'formData.is_checked'(val) {
      if (val === false) {
        this.formItem[4].show = false
      } else {
        this.formItem[4].show = this.formData.has_unusual_status
      }
    },
    'formData.has_unusual_status'(val) {
      this.formItem[4].show = this.formData.has_unusual_status
    }
  },
  components: {
    MyForm
  },
  created() {
    this.formInit()
    this.form.submitDisabled = this.btnDisabled
  },
  methods: {
    getData() {
      let data = Object.assign({}, this.byzInfo)
      this.formData = data
    },
    byzSubmit(val) {
      this.$emit('byzSubmit', val)
    },
    formInit(row) {
      this.formItem = getFormField('byzForm', 'item')
      this.formData = row ? row : getFormField('byzForm', 'data')
    },
    reset() {
      this.$emit('searchFun')
    }
  }
}
</script>
<style lang="scss" scoped>
.byz_form {
  width: 50%;
}
</style>
